#table{
  width: 100%;
}
table {
  width: 100%;
  border-collapse: collapse; /* 合并边框 */
  border: 1px solid #000; /* 外边框 */
}

th, td {
  border: 1px solid #000; /* 内边框 */
  height: 60px; /* 行高 */
  padding: 4px; /* 内边距 */
  font-size: 14pt;
  text-align: center;
}

tr:nth-child(odd) {
  background-color: #f2f2f2; /* 奇数行背景色 */
}

tr:nth-child(even) {
  background-color: #ffffff; /* 偶数行背景色 */
}